import { isObject, parseString, trimObject } from "../../../common/utils.js";

//#region src/namespaces/rdf/parse/utils.ts
const retrieveAbout = (value) => {
	if (!isObject(value)) return;
	return trimObject({ about: parseString(value["@about"]) ?? parseString(value["@rdf:about"]) });
};

//#endregion
export { retrieveAbout };